@import "../../../sass/base/variables";
@import "../../../sass/base/mixins";

.modalRegistrationClientFromFront {
    position: fixed;
    top: -15px;
    bottom: 0;
    left: 0;
    right: 0;
    min-height: 100vh;
    min-width: 100vw;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 20;

    &__section {
        padding: 20px;
        background: white;
        // border: 1px solid red;
        border-radius: 20px;
        max-width: 450px;

        h1 {
            font-family: "Montserrat", sans-serif;
        }
    }
    &__closeicon {
        display: flex;
        justify-content: right;
        margin-bottom: 15px;
        cursor: pointer;
    }

    &__price {
        display: flex;
        justify-content: space-between;
    }
    &__info {
        margin: 20px 0 15px 0;
    }
    &__form {
        margin: 10px 0 30px 0;
        display: grid;
        justify-content: center;
        grid-template-columns: 1fr;
        grid-template-rows: repeat(6, 50px);
        row-gap: 10px;
        // padding: 0 20px;

        .promocode {
            position: relative;

            display: grid;
            grid-template-columns: 55% 40%;
            justify-content: space-between;

            label {
                position: absolute;
                z-index: 5;
                left: 11px;
                top: -5px;
                font-size: 10px;
                font-weight: bold;
                padding: 2px 4px;
                background: rgb(215, 212, 212);
                border-radius: 5px;
                color: rgb(216, 16, 16);
            }

            div {
                background: #ecc91a;
                border-radius: 25px;
                border: none;
                color: #000000;
                display: flex;
                justify-content: center; /* Горизонтальное центрирование */
                align-items: center;
                cursor: pointer;
            }
        }

        .intl-tel-input {
            padding-left: 5px;
            padding-top: 6px;
            border-radius: 25px;
            border: none;
            background: rgb(215, 212, 212);
            z-index: 10;

            &:focus {
                outline: none;
                border: 1px solid $main-color; /* Можно задать стили рамки в фокусе по вашему выбору */
                /* Другие стили, если необходимо */
            }
        }

        input {
            padding-left: 15px;
            border-radius: 25px;
            border: none;
            background: rgb(215, 212, 212);
            &:focus {
                outline: none;
                border: 1px solid $main-color; /* Можно задать стили рамки в фокусе по вашему выбору */
                /* Другие стили, если необходимо */
            }
        }

        button {
            background: #ecc91a;
            border-radius: 25px;
            border: none;
            color: #000000;
        }
    }

    &__policy {
        font-size: 12px;
        margin-bottom: 10px;
        color: rgba(0, 0, 0, 0.7);
    }
}

@media (max-width: 576px) {
    .modalRegistrationClientFromFront {
        &__section {
            display: grid;
            grid-template-columns: 1fr;
            margin: 0 10px;
        }

        &__form {
            grid-template-rows: repeat(6, 50px);
        }
    }
}

@media (max-width: 365px) {
    .modalRegistrationClientFromFront {
        &__section {
            display: grid;
            grid-template-columns: 1fr;
            margin: 0 10px;
            max-width: 320px;
        }

        &__form {
            grid-template-rows: repeat(6, 50px);
            grid-template-columns: 270px;
            font-size: 13px;
        }
    }
}
